import { useState } from "react";
import { useToasts } from "react-toast-notifications";

const Login = (props) => {
  const { addToast } = useToasts();
  const [password, setPassword] = useState(null);

  return (
    <>
      <h1 className="title">Login</h1>
      <div className="tip-area">
        <p>
          Hi! Enter the password below to proceed. The password is encrypted on
          the book. Use the wrapped object to help you!
        </p>
      </div>
      <form>
        <input
          type="text"
          placeholder="Enter the password"
          className="text"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <input
          type="button"
          value="Login"
          className="button"
          onClick={(e) => {
            e.preventDefault();
            if (password === "EuTeAmoMuito!!!") {
              addToast("Congrats!", {
                appearance: "success",
                autoDismiss: true,
              });
              setTimeout(() => {
                props.setToken("Ol");
              }, 1000);
            } else
              addToast("Incorrect password!", {
                appearance: "error",
                autoDismiss: true,
              });
          }}
        />
      </form>
    </>
  );
};

export default Login;
